<template>
  <div class="container">
    <div id="cube" @click="animate">
      <div class="front">
        <span class="dot dot1"></span>
      </div>
      <div class="back">
        <span class="dot dot1"></span>
        <span class="dot dot2"></span>
      </div>
      <div class="right">
        <span class="dot dot1"></span>
        <span class="dot dot2"></span>
        <span class="dot dot3"></span>
      </div>
      <div class="left">
        <span class="dot dot1"></span>
        <span class="dot dot2"></span>
        <span class="dot dot3"></span>
        <span class="dot dot4"></span>
      </div>
      <div class="top">
        <span class="dot dot1"></span>
        <span class="dot dot2"></span>
        <span class="dot dot3"></span>
        <span class="dot dot4"></span>
        <span class="dot dot5"></span>
      </div>
      <div class="bottom">
        <span class="dot dot1"></span>
        <span class="dot dot2"></span>
        <span class="dot dot3"></span>
        <span class="dot dot4"></span>
        <span class="dot dot5"></span>
        <span class="dot dot6"></span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Dice',
  props: {
    availableValues: Array,
    selectedId: String,
  },
  data() {
    return {
      min: 1,
      max: 24,
      clickable: true,
    };
  },
  methods: {
    animate() {
      if (!this.clickable) {
        return;
      }
      this.clickable = false;
      const { min } = this;
      const { max } = this;
      function getRandom(mmax, mmin) {
        return (Math.floor(Math.random() * (mmax - mmin)) + mmin) * 90;
      }
      const cube = this.$el.querySelector('#cube');
      const xRand = getRandom(max, min);
      const yRand = getRandom(max, min);
      cube.style.webkitTransform = `rotateX(${xRand}deg) rotateY(${yRand}deg)`;
      cube.style.transform = `rotateX(${xRand}deg) rotateY(${yRand}deg)`;
      setTimeout(() => {
        this.$parent[this.selectedId] = this.availableValues[
          Math.floor(Math.random() * this.availableValues.length)
        ];
        this.clickable = true;
      }, 6200);
    },
  },
};
</script>

